import {
   ADD_BOOK,
   GET_BOOKS,
   REMOVE_BOOK,
   BOOK_ERROR,
   SET_CURRENT
 
} from '../types';
export default (state, action) => {
   switch (action.type) {
      case ADD_BOOK:
         return {
            ...state,
            books: [action.payload, ...state.books],
            loading: false
         };

      case GET_BOOKS:
         return {
            ...state,
            books: action.payload,
            loading: false
         };
      case REMOVE_BOOK:
         return {
            ...state,
            books: state.books.filter(
               book => book._id !== action.payload
            ),
            loading: false
         };
         case SET_CURRENT:
            return {
               ...state,
               current: action.payload
            };
         case BOOK_ERROR:
            return {
               ...state,
               error: action.payload
            };
         default:
            return state;
      }
   };
   