import React, { Fragment, useContext } from 'react';
import { Link } from 'react-router-dom';
import AuthContext from '../../context/auth/authContext';
import ContactContext from '../../context/contact/contactContext';

import './SideDrawer.css';

const SideDrawer = props => {
   const authContext = useContext(AuthContext);
   const { isAuthenticated, logOut, user } = authContext;

   const contactContext = useContext(ContactContext);
   const { clearContacts } = contactContext;

   const onLogOut = () => {
      logOut();
      clearContacts();
   };

   const authLinks = (
      <Fragment>
         <li><i className="far fa-smile" /> {user && `Hello, ${user.username}`}</li>
         <li>
            <Link to="/contacts">    <i className="fas fa-id-card-alt"  />
               <span className="hide-sm" style={{ marginLeft: '10px' }}>Contactos</span></Link>
         </li>
         <li>
            <Link to="/uploadimage"><i className="fas fa-edit" />
               <span className="hide-sm" style={{ marginLeft: '10px' }}>Pintar</span></Link>
         </li>
         <li>
            <a onClick={onLogOut} href="#!">
               <i className="fas fa-sign-out-alt" />
               <span className="hide-sm" style={{ marginLeft: '10px' }}>Log Out</span>
            </a>
         </li>
      </Fragment>
   );

   const guestLinks = (
      <Fragment>
       
         <li>
            <Link to="/books">Lecturas</Link>
         </li>
         <li>
            <Link to="/drawings">Dibujos</Link>
         </li>
      
         <li>
            <Link to="/posts">Posts</Link>
         </li>
        
         <li>
            <Link to="/login">SignIn</Link>
         </li>
         <li>
            <Link to="/signup">SignUp</Link>
         </li>
      </Fragment>
   );
   let drawerClasses = 'side-drawer';
   if (props.show) {
      drawerClasses = 'side-drawer open';
   }
   return (
      <nav className={drawerClasses}>
         <ul>
            {isAuthenticated ?
             authLinks : guestLinks}
         </ul>
      </nav>
   );
};

export default SideDrawer;
