import React, { useState, useContext, useEffect } from 'react';
import ContactContext from '../../context/contact/contactContext';

const ContactForm = () => {
   const contactContext = useContext(ContactContext);
   const { addContact, updateContact, clearCurrent, current } = contactContext;

   const [contact, setContact] = useState({
      name: '',
      email: '',
      phone: '',
      type: 'Personal'
   });

   useEffect(() => {
      if (current !== null) {
         setContact(current);
      } else {
         clearFields();
      }
      // eslint-disable-next-line
   }, [contactContext, current]);

   const { name, email, phone, type } = contact;

   const clearFields = () => {
      setContact({
         ...contact,
         name: '',
         email: '',
         phone: '',
         type: 'Personal'
      });
   };

   const clearAll = () => {
      clearCurrent();
   };

   const onChange = ({ target }) =>
      setContact({ ...contact, [target.name]: target.value });

   const onSubmit = event => {
      event.preventDefault();
      if (current === null) {
         addContact(contact);
      } else {
         updateContact(contact);
      }
      clearAll();
   };

   return (
    
     
      <div className="card card-body bg-dark">
        <div className="card-header">
          <i className="fas fa-users 3x"></i>&nbsp; &nbsp;  {current ? 'Actualizar Contacto' : 'Añadir Contacto'}
      </div>
        <div className="card-body">
     
         <form onSubmit={onSubmit} >
       
            <input
               type="text"
               placeholder="Nombre"
               name="name"
               value={name}
               onChange={onChange}
            />
            <input
               type="email"
               placeholder="Email"
               name="email"
               value={email}
               onChange={onChange}
            />
            <input
               type="text"
               placeholder="Teléfono"
               name="phone"
               value={phone}
               onChange={onChange}
            />
            <h4 className="text-white">Tipo de Contacto</h4>
            <input
               type="radio"
               name="type"
               value="Personal"
               checked={type === 'Personal'}
               onChange={onChange}
            />{' '}
            <span className="text-white">Personal</span>{' '}
            <input
               type="radio"
               name="type"
               value="Professional"
               checked={type === 'Professional'}
               onChange={onChange}
            />{' '}
            <span className="text-white">Profesional</span>{' '}
            <div>
               <input
                  type="submit"
                  value="Guardar"
                  className="btn  btn-block"
               />
            </div>
            {current && (
               <div style={{ marginBottom: '10px' }}>
                  <button  className="btn btn-light btn-block" onClick={clearAll}>
                     Borrar
               </button>
               </div>
            )}
         </form>
 </div>
 </div>
   );
};

export default ContactForm;
