import React, { useState, useContext, useEffect } from 'react';
import AlertContext from '../../context/alert/alertContext';
import AuthContext from '../../context/auth/authContext';

const Register = props => {
   const alertContext = useContext(AlertContext);
   const { setAlert } = alertContext;

   const authContext = useContext(AuthContext);
   const { isAuthenticated, singUp, errors, clearErrors } = authContext;

   useEffect(() => {
      if (isAuthenticated) {
         props.history.push('/');
      }
      if (errors === 'User already exists') {
         setAlert(errors, 'danger');
         clearErrors();
      }
      // eslint-disable-next-line
   }, [props.history, isAuthenticated, errors]);

   const [user, setUser] = useState({
      username: '',
      email: '',
      password: '',
      confirmPassword: ''
   });

   const { username, email, password, confirmPassword } = user;

   const onChange = ({ target }) =>
      setUser({ ...user, [target.name]: target.value });

   const onSubmit = event => {
      event.preventDefault();
      if (username === '' || email === '' || password === '') {
         setAlert('Please, enter all fields.', 'danger');
      } else if (password !== confirmPassword) {
         setAlert('Passwords do not match.', 'danger');
      } else singUp({ username, email, password });
   };

   return (
      <div className="col-md-4 mx-auto">
      <div className="card card-body bg-dark">
        <div className="card-header">
          <i className="fas fa-user-plus 3x"></i>&nbsp; &nbsp; Crear cuenta
      </div>
        <div className="card-body">
       
         <form onSubmit={onSubmit}>
            <div className="form-group">
               
               <input
                  type="text"
                  name="username"
                  value={username}
                  placeholder="Nombre usuario"
                  onChange={onChange}
                  className="form-control"
                  aria-describedby="inputGroup-sizing-lg"
                  required
               />
            </div>
            <div className="form-group">
            
               <input
                  type="email"
                  name="email"
                  placeholder="Email"
                  value={email}
                  onChange={onChange}
                  className="form-control"
                  aria-describedby="inputGroup-sizing-lg"
                  required
               />
            </div>
            <div className="form-group">
              
               <input
                  type="password"
                  name="password"
                  value={password}
                  onChange={onChange}
                  placeholder="Password"
                  className="form-control"
                  aria-describedby="inputGroup-sizing-lg"
                  required
                  minLength="6"
               />
            </div>
            <div className="form-group">
              
               <input
                  type="password"
                  name="confirmPassword"
                  placeholder="Confirm password"
                  value={confirmPassword}
                  onChange={onChange}
                  className="form-control"
                  aria-describedby="inputGroup-sizing-lg"
                  required
                  minLength="6"
               />
            </div>
            <input
               type="submit"
               value="Sign Up"
               className="btn  btn-block"
            />
         </form>
      </div>
      </div>
      </div>
   );
};

export default Register;
