import React, { useContext, useEffect, useState } from 'react';
import DrawingContext from '../../context/drawing/drawingContext';
import DrawingList from './DrawingList';
import "./drawings.css";


const Drawings = () => {
   const drawingContext = useContext(DrawingContext);
   const { drawings, getDrawings, loading } = drawingContext;
   const [selectedOption, setSelectedOption] = useState('');

   useEffect(() => {
      getDrawings();
      // eslint-disable-next-line
   }, []);



   const Options = ({ options }) => {
      return (

         options.map(option =>

            <option key={option._id} value={option._id}>
               {option.title}
            </option>)
      );
   }
   let content = <h4>Esperando  datos...</h4>;

   if (loading && !drawings) {
      content = <h4>Esperando  datos...</h4>;
   }

   else if (drawings && drawings.length > 0) {
      content = (
         <section className="app-section container">
                  <div className="form-group top-margin-small">
        <label className="card-selector-label">Dibujos para mostrar</label>
            <select className="card-selector form-control"
               value={selectedOption}
               onChange={e => setSelectedOption(e.target.value)}
            >
               <option>Seleccione por título</option>
               <Options options={drawings} />
            </select>
            </div>
         
            {selectedOption  &&
            <DrawingList drawings={drawings} selectedOption={selectedOption} />
            }
           
        </section>
      );
   }

   return content;
};

export default Drawings;
