import React, { Fragment, useContext } from 'react';
import { Link } from 'react-router-dom';
import AuthContext from '../../context/auth/authContext';
import ContactContext from '../../context/contact/contactContext';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import './Toolbar.css';
const Toolbar = props => {
   const authContext = useContext(AuthContext);
   const { isAuthenticated, logOut, user } = authContext;

   const contactContext = useContext(ContactContext);
   const { clearContacts } = contactContext;

   const onLogOut = () => {
      logOut();
      clearContacts();
   };

   const authLinks = (
      <Fragment>
         <li><i className="far fa-smile" /> {user && `Hola, ${user.username}`}</li>
         <li>
            <Link to="/contacts"><i className="fas fa-id-card-alt" />  <span className="hide-sm">Contacts</span></Link>
         </li>
         <li>
            <Link to="/uploadimage"><i className="fas fa-edit" />  <span className="hide-sm">Pintar</span></Link>
         </li>
         <li>
            <a onClick={onLogOut} href="#!">
               <i className="fas fa-sign-out-alt" />
               <span className="hide-sm">Log Out</span>
            </a>
         </li>
      </Fragment>
   );

   const guestLinks = (
      <Fragment>
      
         <li>
            <Link to="/books">Lecturas</Link>
         </li>
         <li>
            <Link to="/drawings">Dibujos</Link>
         </li>
      
         <li>
            <Link to="/about">About</Link>
         </li>
        
         <li>
            <Link to="/login">SignIn</Link>
         </li>
         <li>
            <Link to="/signup">SignUp</Link>
         </li>
      </Fragment>
   );

   return (
      <header className="toolbar">
         <nav className="toolbar__navigation">
            <div className="toolbar__toggle-button">
               <DrawerToggleButton click={props.drawerClickHandler} />
            </div>
            <div className="toolbar__logo" ><a href="/">QEPWEB</a></div>
            <div className="spacer" />
            <div className="toolbar_navigation-items">
               <ul>
                  {isAuthenticated ?
                     authLinks : guestLinks}
               </ul>
            </div>
         </nav>
      </header>
   );
};



export default Toolbar;