import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import FileBase from 'react-file-base64';
import { createPost, updatePost } from '../../../actions/posts';

const Form = ({ currentId, setCurrentId }) => {
  const [postData, setPostData] = useState({ creator: '', title: '', message: '', tags: '', selectedFile: '' });
  const post = useSelector((state) => (currentId ? state.posts.find((message) => message._id === currentId) : null));
  const dispatch = useDispatch();


  useEffect(() => {
    if (post) setPostData(post);
  }, [post]);

  const clear = () => {
    setCurrentId(0);
    setPostData({ creator: '', title: '', message: '', tags: '', selectedFile: '' });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    if (currentId === 0) {
      dispatch(createPost(postData));
      clear();
    } else {
      dispatch(updatePost(currentId, postData));
      clear();
    }
  };

  return (
    <div className="card card-body bg-dark">
    <div className="card-header">
    <i className="fas fa-dove 3x"></i>&nbsp;&nbsp;{currentId ? `Editando "${post.title}"` : 'Añadir Posts'}
  </div>
    <div className="card-body">
      <form  onSubmit={handleSubmit}>
     
      <div className="form-group">
        <input name="creator" type="text"  placeholder="Autor"   value={postData.creator} onChange={(e) => setPostData({ ...postData, creator: e.target.value })}  className="form-control " />
        </div>
  
        <div className="form-group">
        <input name="title" type="text"  placeholder="Título"   value={postData.title} onChange={(e) => setPostData({ ...postData, title: e.target.value })}  className="form-control "/>
        </div>
        
        <div className="form-group">
        <textarea name="message"  type="text"  placeholder="Localización"   rows={4} value={postData.message} onChange={(e) => setPostData({ ...postData, message: e.target.value })}  className="form-control "></textarea>
        </div>
        
        <div className="form-group">
        <input name="tags"   type="text"  placeholder="Tags (separadas por comas )"   value={postData.tags} onChange={(e) => setPostData({ ...postData, tags: e.target.value.split(',') })} className="form-control " />
        </div>
        <div className="form-group">
          <FileBase type="file" multiple={false} onDone={({ base64 }) => setPostData({ ...postData, selectedFile: base64 })} className="form-control-file d-none"/>
          </div>
          <div className="btn-group d-flex">
        <button className="btn btn-primary btn-sm"   type="submit"  >Enviar</button>
        <button   onClick={clear} className="btn btn-light btn-sm"   >Cancelar</button>
        </div>
      </form>
    </div>
    </div>
  );
};

export default Form;
