import React, { useContext } from 'react';
import BookContext from '../../context/book/bookContext';
import PropTypes from 'prop-types';
const BookDetails = ({ book }) => {
  const bookContext = useContext(BookContext);

   const { deleteBook } = bookContext;

  const { title, author, _id } = book;

  const onDelete = () => {
    deleteBook(_id);
    
 };
  return (
    <li  onClick={()=>onDelete()}>
      <div className="title"><h4><span style={{ marginRight: '10px' }}>Título:</span>"{title}"</h4></div>
      <div className="author"><h4><span style={{ marginRight: '10px' }}>Autor:</span>"{author}"</h4></div>
    </li>
  );
}
BookDetails.propTypes = {
  book: PropTypes.object.isRequired
};
export default BookDetails;