import React, { Fragment, Component } from 'react';
import { BrowserRouter as Router,withRouter, Route, Switch } from 'react-router-dom';
import Toolbar from './components/layout/Toolbar';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Backdrop from './components/Backdrop/Backdrop';
import ContactState from './context/contact/ContactState';
import DrawingState from './context/drawing/DrawingState';
import PostsPage from './components/pages/Posts';
import BookState from './context/book/BookState';
import AuthState from './context/auth/AuthState';
import AlertState from './context/alert/AlertState';
import setAuthToken from './utils/setAuthToken';
import PrivateRoute from './components/routing/PrivateRoute';
import BooksPage from './components/pages/Books';
import ContactsPage from './components/pages/Contacts';
import DrawingsPage from './components/pages/Drawings';
import FileUploadPage from './components/pages/Uploads';
import About from './components/pages/About';
import Register from './components/auth/Register';
import Login from './components/auth/Login';
import Alert from './components/layout/Alerts';
import Images from './components/images/Images';
import UploadImage from './components/images/uploadImage';
import GetImage from './components/images/CreateImage';

import './App.css';
if (localStorage.token) {
   setAuthToken(localStorage.token);
}

class App extends Component {
   state = {
      sideDrawerOpen: false
   };

   drawerToggleClickHandler = () => {
      this.setState((prevState) => {
         return { sideDrawerOpen: !prevState.sideDrawerOpen };
      });
   };

   backdropClickHandler = () => {
      this.setState({ sideDrawerOpen: false });
   };

   render() {
      let backdrop;

      if (this.state.sideDrawerOpen) {
         backdrop = <Backdrop click={this.backdropClickHandler} />
      }
      return (

         <AuthState>
            <ContactState>
               <AlertState>
                  <BookState>
                     <DrawingState>
                        <Router>

                           <Fragment>
                              <div style={{ height: '100%' }}>
                                 <Toolbar drawerClickHandler={this.drawerToggleClickHandler} />
                                 <SideDrawer show={this.state.sideDrawerOpen} />
                                 {backdrop}
                                 <main style={{ marginTop: '64px' }}>
                                    <Alert />
                                    <Switch>
                                    <Route exact path="/" component={PostsPage} /> 
                                       <Route exact path="/about" component={About} />
                                       <Route exact path="/signup" component={Register} />
                                       <Route exact path="/login" component={Login} />
                                       <Route exact path="/drawImage" component={FileUploadPage} />
                                       <Route exact path="/drawings" component={DrawingsPage} />
                                       <Route exact path="/Books" component={BooksPage} />
                                       <Route exact path="/images" component={Images} />
                                       <Route exact path="/uploadimage" component={UploadImage} />
                                       <Route exact path="/images/:id" render={(props) => <GetImage {...props} />} />
                                       <PrivateRoute exact path="/Contacts" component={ContactsPage} />
                                    </Switch>
                                 </main>
                              </div>
                           </Fragment>
                        </Router>
                     </DrawingState>
                  </BookState>
               </AlertState>
            </ContactState>
         </AuthState >

      );
   }
}

export default withRouter(App);