import axios from "axios";
//import { getToken } from "./tokenUtils";

const SERVER_URL = "http://www.qepweb.es";
axios.defaults.baseURL = SERVER_URL;

/* const setAuthHeader = () => ({
  headers: { authorization: `Bearer ${getToken()}` }
}); */
export const getImages = payload => axios.get(`/api/images`, payload);
export const createImage = payload => axios.post(`/api/images`, payload);
export const getImage = (ImageId, payload) => axios.get(`/api/images/${ImageId}`);
export const updateImage = (ImageId, payload) => axios.put(`/api/images/${ImageId}`, payload);
export const deleteImage = ImageId => axios.delete(`/api/images/${ImageId}`);
export const getBook = (BookId, payload) => axios.get(`/api/Books/${BookId}`);

//Posts
export const fetchPosts = () => axios.get(`/api/posts`);
export const createPost = (newPost) => axios.post(`/api/posts`, newPost);
export const likePost = (id) => axios.patch(`/api/posts/${id}/likePost`);
export const updatePost = (id, updatedPost) => axios.patch(`/api/posts/${id}`, updatedPost);
export const deletePost = (id) => axios.delete(`/api/posts/${id}`);